*{
    box-sizing: border-box;
    font-family: sans-serif;
}

body{
    background: linear-gradient(to bottom, rgb(59, 61, 90) 0%, rgb(33, 144, 210) 100%);
    background-repeat: no-repeat;
    background-attachment: fixed;
    color: white;
}
.main-container{
    max-width: 980px;
    margin: 10px auto;
    padding:20px;
    height: 100vh;
}

li{
    list-style: none;
}

//*****helpers*****// 
.flex{
    display: flex;
}
.flex-col{
    flex-direction: column;
}
.flex-row{
    flex-direction: row;
}
.align-center{
    align-items: center;
}
.space-around{
    justify-content: space-around;
}
.flex-center{
    justify-content: center;
}
.grid{
    display: grid;
}
.text-center{
    text-align: center;
}