.main-weather{
    margin-top: 20px;
}

.date-location{
    h1{
        font-size: 2rem;
        margin:0
    }
   
}
.degree-summary :first-child{
    font-size: 5.5rem;
}
.degree-summary :last-child{
    font-size: 1.5rem;
}

.stat-val{
font-size: 1.8rem;
}
.stat-label{
    color: lightslategray;
    font-size: 1.4rem;
}

.main-icon{
    height: 160px;

}
.weather-details{
    display: grid;
    grid-template-columns: 50% 50%;
}
.temp-stat{
    display: grid;
    grid-gap: 20px;
    grid-template-columns: auto auto auto;
}
.stat > div{
    margin-bottom: 5px;
}
.temp-stat > div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
}
.divider{
    border-right: 1px solid #d3d3d361;
}
@media screen and (max-width: 650px){
    .weather-details{
       grid-template-columns: none;
        grid-template-rows: 50% 50%;
    }
    .first-panel{
        border-bottom: 1px solid #d3d3d361;
    }
    .second-panel{
        border-bottom: 1px solid #d3d3d361;
    }
    .temp-stat{
        grid-gap: 0;
    }
    .divider{
        border-right: none;
    }
   
   
}