.today-details{
    width: 125px;
    padding: 15px;
    background-color: #305e8a;
    border-radius: 10px;
    margin: 0;
    height: 155px;
    // margin-left: -10px;
}
.today-detail{
    font-size: 1.3rem;
    height: 10px;
}
@media screen and (max-width: 820px){
    .today-details{
        width: 115px;
    }
    
}

@media screen and (max-width: 740px){
    .today-details{
        width: 100px;
        margin-bottom: 10px;
    }
    .today-detail{
        font-size: 1.03rem;
       
    }
   
}