.next-details{
    background-color: #305e8a;
    border-radius: 10px;
    height: 75px;
    padding: 10px;
    margin-bottom: 10px;
  
}

.items{
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.item-val{
    font-size: 1.3rem;
 
}
.item-label{
    color: lightslategray;
    margin-top: 5px;
}
@media screen and (max-width:480px){
   
    .item-val {
        font-size: 1rem;
    }
    .items{
        img{
            height: 90px;
        }
    }
}